@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
html, body {
    
    margin: 0;
    padding: 0;
}

*, *::before, *::after {
    box-sizing: inherit; /* Ensure all elements use border-box sizing */
}

.introcard {
    margin-top: 1%;
    background-color: rgb(255, 255, 255);
    font-size: 18px;
    
    width: 970px;
    height: 430px; /* Use viewport height units for reliable height */
    border-radius: 0px;
    display: flex; /* Use flexbox to properly handle children layout */
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: center;
    font-size: 60px;
    font-family: "Quicksand";
    font-weight: 900;
    font-style: italic;
    background-image: url("/public/landingpage/intro-banner-mid.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size:cover;
  
}

.introcard .leftside,
.introcard .rightside {
    width: 47.5%;
    display: flex;
    align-items: center; /* Vertically center content */
    justify-content: center; /* Horizontally center content */
}

.introcard .rightside {
    padding-left: 5%;
}
.portfolio .leftside
{
    background-image: url("/public/landingpage/cyber image/cyber.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size:cover;
}
.portfolio .rightside
{
    background-image: url("/public/landingpage/photography image/photography.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size:cover;
}
.portfolio {
    margin-top: 1%;
    background-color: rgb(255, 255, 255);
    font-size: 18px;
    width: 970px;
    height: 370px; /* Use viewport height units for reliable height */
    border-radius: 0px;
    display: flex; /* Use flexbox to properly handle children layout */
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;

    
}
.rightside button
{
    position: relative;
    box-shadow: 0 0px 8px 5px rgb(255, 255, 255);
   /* margin-top: 290px;   
    margin-left: 280px;*/
    cursor:pointer; 
    color: white;
    border: none;
    background-color: #f1356d;
    background-color: #7c7c7c;
    border-radius: 8px;
    height:60px;
    width:120px;
    font-weight: bold;
}
.leftside button
{
    position: relative;
    box-shadow: 0 0px 8px 5px rgb(255, 255, 255);
    /*margin-top: 290px;   
    margin-left: 330px;  */
    cursor:pointer;

    color: white;
    border: none;
    background-color: #f1356d;
    border-radius: 8px;
    height:60px;
    width:120px;
    font-weight: bold;
}
.portfolio .leftside,
.portfolio .rightside {
    box-shadow: 0 8px 6px -3px rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);
    border-radius: 0px;
    width: 49%;
    height: 100%;
    display: flex;
    align-items: center; /* Vertically center content */
    justify-content: center; /* Horizontally center content */
    box-sizing: border-box; /* Include padding and border in dimensions */
}

.portfolio .rightside {
    box-shadow: 0 8px 6px -3px rgb(255, 255, 255);
    background-color: rgb(255, 255, 255);

    padding-left: 5%;
}

.link-wrapper {
    display: inline;
    height: 100%;
    width: 100%;
}

@media only screen and (max-width: 860px) 
{
  body 
  {
    background-color: rgb(255, 255, 255);
  }
  .introcard
  {
    width: 95%;
    background-image: url("/public/landingpage/intro-banner-mid.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size:cover;
    background-size: contain;
    margin-top: 0px;
  }
  .portfolio {
    margin-top: 1%;
    background-color: rgb(255, 255, 255);
    font-size: 18px;
    width: 95%;
    height: 470px; /* Use viewport height units for reliable height */
    border-radius: 0px;
    display: flex; /* Use flexbox to properly handle children layout */
    flex-direction: column; /* Change flex direction to column */
    justify-content: space-between;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}

.portfolio .leftside,
.portfolio .rightside {
    box-shadow: 0 8px 6px -3px rgb(207, 207, 207);
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 100%; /* Adjust height to fit both elements in the container */
    display: flex;
    align-items: center; /* Vertically center content */
    justify-content: center; /* Horizontally center content */
    box-sizing: border-box; /* Include padding and border in dimensions */
    padding: 0px;
    margin: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.portfolio .rightside {
    margin-top: 0px; /* Add some space between the two elements */
}
}
