@media only screen and (max-width: 1280px) 
{
    body 
    {
    background-color: rgb(255, 255, 255);
    }
    .content {
    margin-left: 250px;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 0px;
    height: 100%;
    padding-top: 100px;
    }
    .Post
    {
    margin-right: 220px;
    overflow-wrap: break-word;
    }
    .Post img{
    max-width: 100%;
    border-radius: 2px;
    border-color: #d8d8d8 ;
    }
    .sidebarRight
    {
    height: 100%;
    width: 200px;
    border-left: #d8d8d8 solid 1px;
    float: right;
    } 
}