/* mobile */
@media only screen and (max-width: 768px) 
{
  html, body {
    height: 100%;
    margin: 0;
    padding: 0;
}
  body 
  {
    background-color: rgb(255, 255, 255);
  }
.sidebarRight
{
  height: 100%;
  width: 100%;
  border-left: #d8d8d8 solid 1px;
  float: right;
  display: none;
} 
.content {
  margin-left: 0px;
  height: 100%;
  width: 100%;
  padding-left: 0px;
  padding-right: 0px;
}

.Post
{
  width: 96%;
  margin-left: auto;
  margin-right: auto;
}
.my-single-menu
{
  background-color: rgb(253, 253, 253);
  font-size: 18px;
  box-shadow: 0 8px 6px -3px rgb(207, 207, 207);
  width: 100%;
  min-height: 50px;
  height: 100%;
  color: #f1356d;
}
}