@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100&family=Roboto+Mono:ital,wght@0,100;0,100..700;0,200;1,100..700&family=Roboto:ital,wght@0,400;1,100&display=swap');
/* base styles */
*{
* {
  margin-top: 0px;
  margin: 0px;
  padding-top: 0px;
  font-family: "Quicksand";
  color: #333;
  
  /*font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;*/
}
.index{
  color: blue;
}
.wrapper
{
  display:grid;
  grid-template-rows: repeat(4, [row] auto  );
  grid-template-columns: auto;
  display: inline-grid;
  border: #272727 2px ;
}
table {
  display: table; 
  border-collapse: collapse;
  box-sizing: border-box;
  text-indent: initial;
  border-spacing: 0px;
  border: 2px;
  width: 100%;
}
a
{
  color: #f1356d;
  text-decoration: none; /* toglie il sottolineato dei link*/
  width: auto;
  /*display: contents;  /* Shrinks the <a> to the size of its content (the card) */
}
a:visited
{
  color: none;
}
a li
{
  color:#f1356d;
}

hr
{
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0,0,0,.1);
}
#banner hr
{

  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0,0,0,.1);
}

td
{
  vertical-align: top;
  border: solid 1px rgb(205,205,205);
}
li
{
  padding-bottom: 10px;
}
code
{
  /*background-color: rgb(223, 255, 228);*/
  background-color: rgb(54, 54, 54);
  color: white;
  border-radius: 4px;
  padding-left: 3px;
  padding-right: 3px;
  padding-bottom: 3px;
}

/* per i sottotitoli dentro Index dei blog post */
ul ul
{
  font-size: 15px;
  font-style: italic;
}
.Post ul
{
  margin-left: -20px;
}
.Post li
{
  margin-bottom: -8px;
}
.Post
{
  margin-right: 450px;
}
.my-single-menu
{
  background-color: rgb(253, 253, 253);
  font-size: 18px;
  box-shadow: 0 8px 6px -3px rgb(207, 207, 207);
  max-width: 600px;
  height: auto; /* it was 50px now is auto because of the image fit keep image consistent*/
  color: #f1356d;
  background-color: rgb(250, 250, 250);
}
.my-single-menu img
{
  width: 600px;
  height: 200px;
  object-fit: cover;
}
a.my-single-menu
{
  display: inline-block;  /* Shrinks the <a> to the size of its content (the card) */
  text-decoration: none;  /* Remove underline if needed */
}
.my-single-menu post-date
{
  font-size: 13px;
}
.content {
  margin-left: 220px;
  margin-right: 0px;
  padding-left: 50px;
  padding-right: 50px;
  height: 100%;
  padding-top: 100px;
}
.Post p
{
  margin-top: 15px;
  margin-bottom: 15px;
}
.Post img{
  max-width: 100%;
  border: solid 2px;
  border-color: #d8d8d8;
  display:block;
}
#banner {
  width: 100%;           /* Full width of the parent */
  height: 60vh;         /* 50% of the viewport height (adjust as needed) */
  overflow: hidden;      /* Hide overflow to ensure cropping */
  position: sticky;    /* Set position for absolute positioning of the image */
  z-index: 1;
}

#banner img {
  position: absolute;      /* Use absolute positioning */
  top: 50%;                /* Center vertically */
  left: 50%;               /* Center horizontally */
  width: 100%;             /* Make the image take the full width */
  height: auto;            /* Let height adjust automatically to maintain aspect ratio */
  min-height: 100%;        /* Ensure minimum height is 100% to cover the container */
  object-fit: cover;       /* Cover the container without maintaining aspect ratio */
  transform: translate(-50%, -50%); /* Center the image from the middle */
  z-index: 1;
}
.sidebarRight { 
  width: 480px; /* Keep the desired width */
  z-index: 1000; /* Choose a sensible z-index */
  position: fixed; /* This fixes the sidebar to the right of the viewport */
  right: 0; /* Aligns it to the right side */
  top: 0; /* Aligns it to the top */
  /*overflow-y: auto; /* Allow scrolling within the sidebar if content overflows */
  background-color: white; /* Optional: background color to make it distinct */
  margin-top: 100px;
}
.sidebarRight .sidebarRightChild
{
  position: fixed;
  height: auto;
  padding-left:  10px;
  padding-right: 0px;
  display: inline-block;
  max-width: 450px;
  overflow-wrap: break-word;
  border-left: #d8d8d8 solid 1px; 
}
.sidebarRight .sidebarRightChild .tags
{
  margin-top: 0px;
  display: flex;
  flex-flow: row wrap;
  width: auto;
  /*justify-content: space-evenly;*/
}
.sidebarRight .sidebarRightChild my-tag
{
  color: rgb(255, 255, 255);
  background-color: #f1356d;
  border-radius: 8px;
  padding-left: 4px;
  padding-right: 4px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin-left: 3px;
  margin-right: 3px;
  margin-top: 5px;
}
.sidebarLeft  
{
  position:fixed;
  background-color: rgb(250, 250, 250);
  height: 100%;
  width: 250px;
  transition: transform 0.3s ease; /* Added for smooth sliding */
  z-index: 2;
}
.sidebarLeft li:hover 
{
  background-color: #f1356d
}

.sidebarLeft a li:hover 
{
  color: white;
}
.sidebarLeft a li:focus
{
  color: rgb(63, 9, 9);
}

.sidebarLeft .avatar 
{
  margin: auto;
}

.sidebarLeft .avatar img
{
  display: block;
  margin-top: 30px;
  border-radius: 50%;
  height: 170px;
  width: 170px;
  border-color: #272727;
  border-style: solid;
  border-width: 7px;
  margin-left: auto;
  margin-right: auto;
}

.sidebarLeft ul
{
  display: block;
  padding: 0px; /* it is needed */
  padding-top: 30px;
}
.sidebarLeft li
{
  padding-left: 30px;
  padding-top: 15px;
  list-style-type: none;
  height: 30px;
}

